import React from "react";
import { useState, useEffect } from "react";
import { drums } from "./Data";

const Drum = () => {
  const [power, setPower] = useState(false);
  const [display, setDisplay] = useState("");
  const [hover, setHover] = useState(false);

  useEffect(() => {
    document.addEventListener("keydown", logKey);
    function logKey(e) {
      let found = false;
      let val = e.key.toUpperCase();
      for (var i = 0; i < drums.length; i++) {
        if (drums[i].key === val) {
          found = true;
          drumPlay(val);
          break;
        }
      }
    }
    return () => window.removeEventListener("keydown", logKey);
  }, [display]);

  const drumPlay = (key) => {
    if (power === true) {
      setDisplay(key);
      document.getElementById(key).currentTime -= 30;
      document.getElementById(key).play();
    }
  };

  return (
    <div id="drum-machine">
      <div id="keys">
        {drums.map((drum) => {
          return (
            <button
              id={"pad " + drum.key}
              className="drum-pad"
              onClick={() => drumPlay(drum.key)}
              onMouseEnter={() => setHover(true)}
              onMouseLeave={() => setHover(false)}
            >
              <audio
                id={drum.key}
                autoPlay={false}
                src={drum.sound}
                display="none"
              />
              {drum.key}
            </button>
          );
        })}
      </div>
      <div id="display">
        <h3>POWER</h3>
        <label class="switch">
          <input type="checkbox" onChange={() => setPower(!power)} />
          <span class="slider"></span>
        </label>
        <p id="current">{power === true ? "PAD " + display : ""}</p>
      </div>
    </div>
  );
};

export default Drum;
